*,
*::after,
*::before {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  cursor: pointer;
  background: transparent;
  border: none;
  text-transform: uppercase;
  text-decoration: none;
}
.link {
  display: block;
